body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Normal Theme */
html[data-theme="normal"] {
  --primary-color: #EA1039;
  --secondary-color: rgba(27, 20, 100,1);
  --dark-color: rgb(14, 14, 14);
  --text-color: black;
  --link-color: #007bff;
  --profile-page-input-border-color : white;
  --theme-background-color : white;
  --modal-no-btn-color:grey;
  --modal-no-btn-hover-color:rgb(97, 97, 97);
  --toast-color:#EA1039;
  --toast-border-color:#EA1039;
  --profile-page-tabs-shadow-color:rgba(0, 0, 0, 0.438);
  --skeleton-loader-first-color: #eee;
  --skeleton-loader-second-color: #f5f5f5;
  --drop-links:black;
  --hr-divider-color:rgba(0, 0, 0, 0.1);
  /* --profile-dropdown-hover-color: rgb(231, 231, 231); */
  --profile-dropdown-hover-color: #ea10381a;
  --chat-list-hover-color: #ea10381a;
  /* --profile-dropdown-hover-color: rgba(27, 20, 100, 0.247); */
  --chat-list-item-border-color : #eee;
}
/* Dark Theme */
html[data-theme="dark"] {
  --primary-color: #EA1039;
  --secondary-color: rgba(27, 20, 100,1);
  --dark-color: rgb(14, 14, 14);
  --text-color: white;
  --link-color: white;
  --profile-page-input-border-color : rgb(14, 14, 14);
  --theme-background-color : rgb(14, 14, 14);
  --modal-no-btn-color:grey;
  --modal-no-btn-hover-color:rgb(97, 97, 97);
  --toast-color: rgb(14,14,14);
  --toast-border-color:white;
  --profile-page-tabs-shadow-color: rgb(14, 14, 14);
  --skeleton-loader-first-color: #474747;
  --skeleton-loader-second-color: #2c2c2c;
  --drop-links:white;
  --hr-divider-color:white;
  --profile-dropdown-hover-color: rgb(59, 59, 59);
  /* --profile-dropdown-hover-color: rgb(218, 205, 205); */
  --chat-list-hover-color: rgb(59, 59, 59);
  /* --profile-dropdown-hover-color: #ea10381a; */
  --chat-list-item-border-color : white;
}
