/* Comments Toggle Switch Css */
.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 21px;
  /* border: 1px solid black; */
  border-radius: 34px; 
  top: 4px;
}
.details-switch{
  position: relative;
  display: inline-block;
  width: 30px;
  height: 16px;
  /* border: 1px solid black; */
  border-radius: 34px; 
  top: 4px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}


.slider:before {
  position: absolute;
  content: "";
  height: 17px;
  width: 16px;
  top: 2px;
  left: 2px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}
.details-slider:before{
  height: 12px;
  width: 12px;
}

input:checked + .slider {
  background-color: #EA1039;
  /* background-color: rgba(27, 20, 100,1); */
}

 /* input:focus + .slider {
   box-shadow: 0 0 1px #2196F3;
} */

input:checked + .slider:before {
  transform: translateX(20px);
}
input:checked + .details-slider:before {
  transform: translateX(14px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
